    font-family: "Hiragino Kaku Gothic ProN", 'Noto Sans JP', sans-serif !important;
}
input[data-v-5ec9ceaa]::-webkit-input-placeholder, textarea[data-v-5ec9ceaa]::-webkit-input-placeholder {
    color: #bdbdbd !important;
}
input[data-v-5ec9ceaa]::-moz-placeholder, textarea[data-v-5ec9ceaa]::-moz-placeholder {
    color: #bdbdbd !important;
}
input[data-v-5ec9ceaa]::-ms-input-placeholder, textarea[data-v-5ec9ceaa]::-ms-input-placeholder {
    color: #bdbdbd !important;
}
input[data-v-5ec9ceaa]::placeholder, textarea[data-v-5ec9ceaa]::placeholder {
    color: #bdbdbd !important;
}

/******************** .container ********************/
.container[data-v-5ec9ceaa] {
    margin: 0 auto;
    max-width: 1280px;
    width: 90%;
}
@media only screen and (min-width: 601px) {
.container[data-v-5ec9ceaa] {
    width: 85%;
}
}
@media only screen and (min-width: 993px) {
.container[data-v-5ec9ceaa] {
    width: 70%;
}
}

/******************** .container-100 ********************/
.container-100[data-v-5ec9ceaa] {
    margin: 0 auto;
    max-width: 1280px;
    width: 100%;
}
@media only screen and (min-width: 601px) {
.container-100[data-v-5ec9ceaa] {
    width: 85%;
}
}
@media only screen and (min-width: 993px) {
.container-100[data-v-5ec9ceaa] {
    width: 70%;
}
}

/******************** .container-sub ********************/
.container-sub[data-v-5ec9ceaa] {
    width: 90%;
    margin: 0 auto;
}
@media only screen and (min-width: 601px) {
.container-sub[data-v-5ec9ceaa] {
    width: 100%;
}
}

