/******************** Materialize Tabs Override ********************/
.tabs[data-v-5ec9ceaa] .tab a{
    color:#9e9e9e;
}
.tabs[data-v-5ec9ceaa] .tab a:hover {
    background-color: transparent;
    /*color: #000;*/
}
.tabs[data-v-5ec9ceaa] .tab a.active {
    background-color: transparent;
    color: #2196f3;
}
.tabs[data-v-5ec9ceaa] .indicator {
    /*background-color: #000;*/
    background: -webkit-gradient(linear, left top, right top, from(#ffffff), color-stop(40%, #ffffff), color-stop(40%, #2196f3), color-stop(60%, #2196f3), color-stop(60%, #ffffff),to(#ffffff));
    background: linear-gradient(90deg, #ffffff 0%, #ffffff 40%, #2196f3 40%, #2196f3 60%, #ffffff 60%,#ffffff 100%);
}
.tabs[data-v-5ec9ceaa] .tab a:focus {
    background-color: transparent;
    outline: transparent;
}
.tabs[data-v-5ec9ceaa] .tab a:focus.active {
    background-color: transparent;
    outline: transparent;
}

